/* SignIn.module.css */

.signInContainer {
  max-width: 300px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f9f9f9;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.notificationBanner {
  background-color: #ffd700; /* Yellow background */
  color: #333; /* Dark text color */
  padding: 10px;
  border-radius: 5px;
  text-align: center;
  margin-bottom: 10px; /* Add some space below the banner */
}

.notificationBanner strong {
  font-weight: bold;
}

.googleButton {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  background-color: #4285f4; /* Google blue */
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 10px; /* Add space between banner and button */
}

.googleButton:hover {
  background-color: #357ae8; /* Darker shade of Google blue */
}

.googleButton:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.googleButton > svg {
  margin-right: 10px;
}
